@forward 'typography';
@forward 'colors';
@use 'layout';
@use 'utils';

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.attribution {
  font-size: 11px;
  text-align: center;

  margin-top: utils.pxToRem(40px);

  &__content {
    background-color: var(--white);
    padding: layout.space('xs');
    border-radius: layout.border-radius('s'); // could probably be static 20px
  }

  a {
    color: hsl(228, 45%, 44%);
  }
}

body {
  background-color: var(--light-grey);
}

.presentation-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;

  &__filler {
    flex: 1;
  }
}

.presentation-content {
  margin: auto;
}

.card {
  max-width: utils.pxToRem(320px);
  max-height: utils.pxToRem(497px);
  padding: layout.space('s');

  background-color: var(--white);
  border-radius: layout.border-radius('m'); // could probably be static 20px

  &__img {
    img {
      display: block;

      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;

      border-radius: layout.border-radius('s');
    }
  }

  &__content {
    margin-top: layout.space('m');
    margin-bottom: layout.space('m');

    text-align: center;
  }

  &--shadow {
    box-shadow: 0px utils.pxToRem(25px) 25rem hsla(0deg, 0%, 0%, 4.77%);
  }
}

.pad-s {
  padding: 0 layout.space('s');
}

.debug {
  border: 1px solid red;
}
